@import "./../../../../../shared/assets/scss/variables";
.ant-picker {
    border-radius: 4px !important;
}
.ant-picker-input {
    z-index: 15;
    height: 100%;
    padding-top: 20px;
   
    & input {
        height: 100%; 
        background: transparent;
        z-index: 20;
        padding-bottom: 4px !important;
        font-size: 14px;
        color: $color-gray-400;
        font-weight: 400
    }
}

.ant-picker-focused {
    box-shadow: none;
    border: 1px solid $color-gray-300
}
.ant-picker:hover, .ant-picker-focused {
    border: 1px solid $color-gray-300
}
